/* ===========================
   ULTIMA 9.0 Onboarding Styles
   Night Sochi Aesthetic
   =========================== */

:root {
  /* Colors - Night Sochi Palette */
  --primary: #6366f1;
  --primary-dark: #4f46e5;
  --secondary: #8b5cf6;
  --accent: #ec4899;
  --success: #10b981;

  --bg-primary: #0f0f1e;
  --bg-secondary: #1a1a2e;
  --bg-tertiary: #252538;
  --bg-glass: rgba(30, 30, 50, 0.7);

  --text-primary: #ffffff;
  --text-secondary: #a0a0b8;
  --text-muted: #6b6b8f;

  --border: rgba(255, 255, 255, 0.1);
  --border-hover: rgba(255, 255, 255, 0.2);

  /* Gradients */
  --gradient-primary: linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%);
  --gradient-secondary: linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%);
  --gradient-accent: linear-gradient(135deg, #06b6d4 0%, #3b82f6 100%);

  /* Spacing */
  --spacing-xs: 0.5rem;
  --spacing-sm: 1rem;
  --spacing-md: 1.5rem;
  --spacing-lg: 2rem;
  --spacing-xl: 3rem;
  --spacing-2xl: 4rem;

  /* Typography */
  --font-body: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, sans-serif;
  --font-heading: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, sans-serif;

  /* Borders */
  --radius-sm: 0.5rem;
  --radius-md: 0.75rem;
  --radius-lg: 1rem;
  --radius-xl: 1.5rem;

  /* Shadows */
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.2);
  --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.3);
  --shadow-glow: 0 0 20px rgba(99, 102, 241, 0.3);
}

/* ===========================
     RESET & BASE
     =========================== */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

/* Respect user's motion preferences */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }

  html {
    scroll-behavior: auto;
  }
}

body {
  font-family: var(--font-body);
  background: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* ===========================
     LAYOUT
     =========================== */

.app {
  min-height: 100vh;
  position: relative;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--spacing-md);
}

.section {
  padding: var(--spacing-2xl) 0;
  position: relative;
}

/* ===========================
     PROGRESS BAR
     =========================== */

.progress-bar {
  position: fixed;
  top: 0;
  left: 0;
  height: 3px;
  background: var(--gradient-primary);
  z-index: 1000;
  transition: width 0.2s ease;
}

/* ===========================
     NAVIGATION
     =========================== */

.navbar {
  position: sticky;
  top: 0;
  z-index: 100;
  background: rgba(15, 15, 30, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--border);
}

.nav-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: var(--spacing-sm) var(--spacing-md);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--spacing-lg);
}

.nav-brand {
  font-size: 1.5rem;
  font-weight: 700;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.5px;
}

.nav-links {
  display: flex;
  gap: var(--spacing-xs);
  align-items: center;
}

.nav-link {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--text-secondary);
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--radius-md);
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  font-weight: 500;
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
}

.nav-link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.1) 0%,
    rgba(139, 92, 246, 0.1) 100%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
}

.nav-link:hover {
  color: var(--text-primary);
  border-color: rgba(99, 102, 241, 0.4);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.2);
}

.nav-link:hover::before {
  opacity: 1;
}

.nav-link.active {
  color: white;
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.3) 0%,
    rgba(139, 92, 246, 0.3) 100%
  );
  border-color: var(--primary);
  box-shadow: 0 4px 16px rgba(99, 102, 241, 0.3);
}

.nav-link.active::before {
  opacity: 1;
}

.mobile-menu-btn {
  display: none;
  background: none;
  border: none;
  color: var(--text-primary);
  cursor: pointer;
  padding: var(--spacing-xs);
}

.mobile-menu {
  display: none;
  flex-direction: column;
  padding: var(--spacing-sm);
  background: var(--bg-secondary);
  border-top: 1px solid var(--border);
}

.mobile-menu-item {
  background: none;
  border: none;
  color: var(--text-secondary);
  padding: var(--spacing-sm);
  text-align: left;
  cursor: pointer;
  transition: all 0.2s ease;
  border-radius: var(--radius-sm);
}

.mobile-menu-item:hover,
.mobile-menu-item.active {
  color: var(--text-primary);
  background: rgba(255, 255, 255, 0.05);
}

/* Mobile Navigation */
@media (max-width: 768px) {
  .nav-menu.desktop-only {
    display: none;
  }

  .mobile-menu-btn {
    display: block;
  }

  .mobile-menu {
    display: flex;
  }
}

/* ===========================
     HERO SECTION
     =========================== */

.hero-section {
  padding: var(--spacing-2xl) 0 calc(var(--spacing-2xl) * 1.5) 0;
  min-height: 85vh;
  display: flex;
  align-items: center;
  background: radial-gradient(
      circle at 20% 50%,
      rgba(99, 102, 241, 0.15),
      transparent 50%
    ),
    radial-gradient(
      circle at 80% 80%,
      rgba(139, 92, 246, 0.15),
      transparent 50%
    );
}

.hero-content {
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}

.hero-badge {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
  padding: var(--spacing-sm) var(--spacing-lg);
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.2) 0%,
    rgba(139, 92, 246, 0.2) 100%
  );
  border: 1px solid rgba(99, 102, 241, 0.4);
  border-radius: var(--radius-xl);
  backdrop-filter: blur(10px);
  color: white;
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: var(--spacing-xl);
  letter-spacing: 0.3px;
  animation: pulse-glow 3s ease-in-out infinite;
  box-shadow: 0 4px 20px rgba(99, 102, 241, 0.3);
}

.hero-badge svg {
  color: var(--primary);
  animation: sparkle 2s ease-in-out infinite;
}

@keyframes pulse-glow {
  0%,
  100% {
    box-shadow: 0 4px 20px rgba(99, 102, 241, 0.3);
  }
  50% {
    box-shadow: 0 4px 30px rgba(99, 102, 241, 0.5);
  }
}

@keyframes sparkle {
  0%,
  100% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(180deg) scale(1.1);
  }
}

.hero-title {
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-weight: 800;
  line-height: 1.1;
  margin-bottom: var(--spacing-lg);
  background: linear-gradient(135deg, #ffffff 0%, #a0a0b8 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-subtitle {
  font-size: clamp(1.1rem, 2.5vw, 1.35rem);
  color: var(--text-secondary);
  margin-bottom: var(--spacing-lg);
  line-height: 1.6;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.hero-accent {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-xl);
  padding: var(--spacing-md) var(--spacing-xl);
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.15) 0%,
    rgba(139, 92, 246, 0.15) 100%
  );
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-lg);
  backdrop-filter: blur(10px);
  font-weight: 600;
  font-size: 1rem;
  color: var(--text-primary);
  flex-wrap: wrap;
}

.hero-accent span {
  white-space: nowrap;
}

.accent-divider {
  color: var(--primary);
  font-size: 1.2rem;
}

.hero-bullets {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-xl);
  text-align: left;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.hero-bullet {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm) var(--spacing-md);
  background: rgba(255, 255, 255, 0.02);
  border-left: 3px solid var(--primary);
  border-radius: var(--radius-sm);
  color: var(--text-secondary);
  font-size: 0.95rem;
  line-height: 1.5;
  transition: all 0.3s ease;
}

.hero-bullet:hover {
  background: rgba(255, 255, 255, 0.05);
  border-left-color: var(--primary);
  transform: translateX(4px);
}

.bullet-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  color: var(--text-secondary);
}

.bullet-item svg {
  color: var(--success);
  flex-shrink: 0;
}

/* ===========================
     SECTION HEADERS
     =========================== */

.section-header {
  text-align: center;
  margin-bottom: var(--spacing-xl);
}

.section-header h2 {
  font-size: clamp(1.75rem, 4vw, 2.5rem);
  font-weight: 700;
  margin-bottom: var(--spacing-sm);
  color: var(--text-primary);
}

.section-subtitle {
  color: var(--text-secondary);
  font-size: 1.125rem;
}

.section-icon {
  color: var(--primary);
  margin-bottom: var(--spacing-sm);
}

/* ===========================
     CARDS & GLASSMORPHISM
     =========================== */

.card {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  transition: all 0.3s ease;
}

.card:hover {
  border-color: var(--border-hover);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.card h3 {
  font-size: 1.5rem;
  margin-bottom: var(--spacing-sm);
  color: var(--text-primary);
}

.card h4 {
  font-size: 1.25rem;
  margin-bottom: var(--spacing-sm);
  color: var(--text-primary);
}

.card h5 {
  font-size: 1.125rem;
  margin-bottom: var(--spacing-sm);
  color: var(--text-primary);
  font-weight: 600;
}

.card p {
  color: var(--text-secondary);
  margin-bottom: var(--spacing-sm);
  line-height: 1.7;
}

.card ul {
  list-style: none;
  padding: 0;
}

.card ul li {
  color: var(--text-secondary);
  padding: var(--spacing-xs) 0;
  padding-left: var(--spacing-md);
  position: relative;
}

.card ul li::before {
  content: "";
  position: absolute;
  left: 0;
  color: var(--primary);
  font-weight: bold;
}

/* ===========================
     TIMELINE
     =========================== */

.timeline {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 30px;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--gradient-primary);
}

.timeline-item {
  display: flex;
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-xl);
  position: relative;
}

.timeline-marker {
  flex-shrink: 0;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.25rem;
  box-shadow: var(--shadow-glow);
  z-index: 1;
}

.timeline-content {
  flex: 1;
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  transition: all 0.3s ease;
}

.timeline-content:hover {
  border-color: var(--border-hover);
  transform: translateX(10px);
}

.timeline-content h3 {
  font-size: 1.25rem;
  margin-bottom: var(--spacing-xs);
  color: var(--text-primary);
}

.timeline-content p {
  color: var(--text-secondary);
}

.notice {
  background: rgba(99, 102, 241, 0.1);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-md);
  padding: var(--spacing-md);
  margin-top: var(--spacing-lg);
  text-align: center;
}

.notice p {
  color: var(--text-secondary);
  margin: 0;
}

/* ===========================
     SECTION BLOCKS (no borders)
     =========================== */

.section-block {
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-xl);
}

.section-block h3 {
  font-size: 1.75rem;
  margin-bottom: var(--spacing-md);
  color: var(--text-primary);
}

.section-block h4 {
  font-size: 1.35rem;
  margin-bottom: var(--spacing-sm);
  color: var(--text-primary);
}

.section-block p {
  color: var(--text-secondary);
  line-height: 1.7;
  margin-bottom: var(--spacing-md);
}

.section-block ul {
  list-style: none;
  padding-left: 0;
}

.section-block ul li {
  padding-left: var(--spacing-lg);
  margin-bottom: var(--spacing-sm);
  position: relative;
  color: var(--text-secondary);
}

.section-block ul li:before {
  content: "";
  position: absolute;
  left: 0;
  color: var(--primary);
  font-weight: 700;
}

/* ===========================
     DOCUMENT CARDS GRID
     =========================== */

.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: var(--spacing-lg);
}

.doc-card {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  text-align: center;
  transition: all 0.3s ease;
}

.doc-card:hover {
  border-color: var(--primary);
  transform: translateY(-5px);
  box-shadow: var(--shadow-glow);
}

.doc-card svg {
  color: var(--primary);
  margin-bottom: var(--spacing-sm);
}

.doc-card h3 {
  font-size: 1.25rem;
  margin-bottom: var(--spacing-xs);
}

.doc-card p {
  color: var(--text-secondary);
  margin-bottom: var(--spacing-md);
}

.doc-status-badge {
  display: inline-block;
  padding: var(--spacing-xs) var(--spacing-sm);
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.2) 0%,
    rgba(139, 92, 246, 0.2) 100%
  );
  border: 1px solid rgba(99, 102, 241, 0.4);
  border-radius: var(--radius-md);
  color: var(--primary);
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: var(--spacing-sm);
}

.doc-link {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
  color: var(--primary);
  text-decoration: none;
  font-weight: 600;
  transition: all 0.2s ease;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 0.95rem;
}

.doc-link:hover {
  color: var(--primary-dark);
  gap: var(--spacing-sm);
}

.doc-link.disabled {
  color: var(--text-muted);
  cursor: not-allowed;
  opacity: 0.6;
}

.doc-link.disabled:hover {
  gap: var(--spacing-xs);
  color: var(--text-muted);
}

/* ===========================
     ACCORDION
     =========================== */

.accordion-container {
  max-width: 800px;
  margin: var(--spacing-lg) auto 0;
}

.accordion-container h3 {
  font-size: 1.5rem;
  margin-bottom: var(--spacing-md);
  text-align: center;
}

.accordion-item {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  margin-bottom: var(--spacing-sm);
  overflow: hidden;
  transition: all 0.3s ease;
}

.accordion-item:hover {
  border-color: var(--border-hover);
}

.accordion-header {
  width: 100%;
  background: none;
  border: none;
  padding: var(--spacing-md);
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  color: var(--text-primary);
  font-size: 1rem;
  font-weight: 600;
  text-align: left;
  transition: all 0.2s ease;
}

.accordion-header:hover {
  background: rgba(255, 255, 255, 0.05);
}

.accordion-content {
  padding: 0 var(--spacing-md) var(--spacing-md);
  color: var(--text-secondary);
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.principle-list {
  list-style: none;
  padding: 0;
}

.principle-list li {
  padding: var(--spacing-sm);
  margin-bottom: var(--spacing-xs);
  background: rgba(255, 255, 255, 0.03);
  border-radius: var(--radius-sm);
  border-left: 3px solid var(--primary);
  color: var(--text-secondary);
}

/* ===========================
     PYRAMID (ROLES)
     =========================== */

.pyramid-container {
  max-width: 900px;
  margin: 0 auto;
}

.pyramid {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
}

.pyramid-level {
  display: flex;
  justify-content: center;
}

.role-card {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  text-align: center;
  transition: all 0.3s ease;
  width: 100%;
  max-width: 600px;
}

.level-1 .role-card {
  max-width: 100%;
}
.level-2 .role-card {
  max-width: 90%;
}
.level-3 .role-card {
  max-width: 80%;
}
.level-4 .role-card {
  max-width: 70%;
}
.level-5 .role-card {
  max-width: 60%;
}

.role-card:hover {
  border-color: var(--primary);
  transform: scale(1.02);
  box-shadow: var(--shadow-glow);
}

.role-card svg {
  color: var(--primary);
  margin-bottom: var(--spacing-sm);
}

.role-card h4 {
  font-size: 1.25rem;
  margin-bottom: var(--spacing-xs);
}

.role-card p {
  color: var(--text-secondary);
  margin: 0;
}

/* ===========================
     PREP SS SECTION (KEY)
     =========================== */

.highlight-section {
  background: radial-gradient(
    circle at 50% 50%,
    rgba(99, 102, 241, 0.1),
    transparent 70%
  );
  padding: var(--spacing-2xl) 0;
}

.prep-tools {
  margin-top: var(--spacing-xl);
}

.prep-tools > h3 {
  font-size: 1.75rem;
  text-align: center;
  margin-bottom: var(--spacing-xl);
}

.tool-card {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);
  transition: all 0.3s ease;
}

.tool-card.highlight {
  border: 1px solid rgba(99, 102, 241, 0.3);
  background: rgba(99, 102, 241, 0.05);
}

.tool-card:hover {
  border-color: var(--primary);
  box-shadow: var(--shadow-glow);
}

.tool-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-md);
}

.tool-header svg {
  color: var(--primary);
  flex-shrink: 0;
}

.tool-header h4 {
  font-size: 1.5rem;
  margin: 0;
}

.tool-content ul {
  list-style: none;
  padding: 0;
  margin: var(--spacing-md) 0;
}

.tool-content ul li {
  padding: var(--spacing-xs) 0;
  padding-left: var(--spacing-md);
  position: relative;
  color: var(--text-secondary);
}

.tool-content ul li::before {
  content: "";
  position: absolute;
  left: 0;
  color: var(--primary);
  font-weight: bold;
}

.meeting-item {
  display: flex;
  gap: var(--spacing-md);
  padding: var(--spacing-md);
  background: rgba(255, 255, 255, 0.03);
  border-radius: var(--radius-md);
  margin-bottom: var(--spacing-sm);
}

.meeting-number {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.25rem;
  color: white;
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.3);
  border: 2px solid rgba(255, 255, 255, 0.1);
}

.meeting-item strong {
  color: var(--text-primary);
  display: block;
  margin-bottom: var(--spacing-xs);
  font-size: 0.95rem;
  line-height: 1.4;
}

.meeting-content {
  flex: 1;
  min-width: 0;
}

.meeting-item p {
  color: var(--text-secondary);
  margin: 0;
  font-size: 0.9rem;
  line-height: 1.5;
}

.ai-features,
.ai-result {
  margin: var(--spacing-md) 0;
  padding: var(--spacing-md);
  background: rgba(255, 255, 255, 0.03);
  border-radius: var(--radius-md);
  border-left: 3px solid var(--primary);
}

.prompt-section {
  margin-top: var(--spacing-lg);
  padding: var(--spacing-lg);
  background: rgba(139, 92, 246, 0.05);
  border: 1px solid rgba(139, 92, 246, 0.3);
  border-radius: var(--radius-lg);
}

.prompt-box {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  padding: var(--spacing-md);
  margin: var(--spacing-md) 0;
  overflow-x: auto;
}

.prompt-box pre {
  color: var(--text-secondary);
  font-family: "Courier New", monospace;
  font-size: 0.875rem;
  margin: 0;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.prompt-hint {
  margin-top: var(--spacing-sm);
  font-size: 0.8rem;
  color: var(--text-muted);
  font-style: italic;
  text-align: center;
}

.prompt-actions {
  display: flex;
  gap: var(--spacing-sm);
  flex-wrap: wrap;
}

.instructions {
  margin-top: var(--spacing-lg);
}

.instruction-item {
  margin-bottom: var(--spacing-md);
  padding: var(--spacing-md);
  background: rgba(255, 255, 255, 0.03);
  border-radius: var(--radius-md);
}

.instruction-item strong {
  display: block;
  color: var(--text-primary);
  margin-bottom: var(--spacing-xs);
}

.instruction-item p {
  color: var(--text-secondary);
  margin: 0;
}

/* ===========================
     RHYTHM GRID
     =========================== */

.rhythm-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: var(--spacing-lg);
}

.rhythm-card {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  transition: all 0.3s ease;
}

.rhythm-card:hover {
  border-color: var(--primary);
  transform: translateY(-5px);
  box-shadow: var(--shadow-glow);
}

.rhythm-card h4 {
  font-size: 1.25rem;
  margin-bottom: var(--spacing-sm);
  color: var(--text-primary);
}

.rhythm-card p {
  color: var(--text-secondary);
  margin: 0;
}

/* ===========================
     BUTTONS
     =========================== */

.cta-button {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
  padding: var(--spacing-sm) var(--spacing-lg);
  border-radius: var(--radius-md);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
  border: none;
  font-size: 1rem;
}

.cta-button.primary {
  background: var(--gradient-primary);
  color: white;
  box-shadow: 0 4px 14px rgba(99, 102, 241, 0.4);
}

.cta-button.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(99, 102, 241, 0.6);
}

.cta-button.primary:focus {
  outline: 3px solid rgba(99, 102, 241, 0.5);
  outline-offset: 2px;
}

.cta-button.secondary {
  background: rgba(99, 102, 241, 0.1);
  color: var(--primary);
  border: 1px solid rgba(99, 102, 241, 0.3);
}

.cta-button.secondary:hover {
  background: rgba(99, 102, 241, 0.2);
  border-color: var(--primary);
}

.cta-button.secondary:focus {
  outline: 3px solid rgba(99, 102, 241, 0.5);
  outline-offset: 2px;
  background: rgba(99, 102, 241, 0.15);
}

/* Focus states for all interactive elements */
button:focus-visible,
a:focus-visible,
.nav-link:focus-visible {
  outline: 3px solid rgba(99, 102, 241, 0.5);
  outline-offset: 2px;
}

.cta-button.large {
  padding: var(--spacing-md) var(--spacing-xl);
  font-size: 1.125rem;
}

/* ===========================
     FOOTER CTA
     =========================== */

.footer-cta {
  background: radial-gradient(
    circle at 50% 50%,
    rgba(139, 92, 246, 0.2),
    transparent 70%
  );
  padding: var(--spacing-2xl) 0;
}

.footer-cta-content {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.footer-cta-content h2 {
  font-size: clamp(2rem, 4vw, 3rem);
  margin-bottom: var(--spacing-sm);
}

.footer-cta-content p {
  color: var(--text-secondary);
  font-size: 1.125rem;
  margin-bottom: var(--spacing-xl);
}

.footer-actions {
  display: flex;
  gap: var(--spacing-md);
  justify-content: center;
  flex-wrap: wrap;
}

/* ===========================
     TOAST NOTIFICATION
     =========================== */

.toast {
  position: fixed;
  bottom: var(--spacing-lg);
  right: var(--spacing-lg);
  background: var(--bg-secondary);
  border: 1px solid var(--success);
  border-radius: var(--radius-md);
  padding: var(--spacing-md) var(--spacing-lg);
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  box-shadow: var(--shadow-lg);
  animation: slideInUp 0.3s ease;
  z-index: 1000;
}

.toast svg {
  color: var(--success);
}

@keyframes slideInUp {
  from {
    transform: translateY(100px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* ===========================
     ANIMATIONS
     =========================== */

.fade-in {
  animation: fadeIn 0.6s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===========================
     RESPONSIVE
     =========================== */

@media (max-width: 768px) {
  .section {
    padding: var(--spacing-xl) 0;
  }

  .hero-section {
    min-height: auto;
    padding: var(--spacing-xl) 0;
  }

  /* Mobile Typography */
  .section-block p,
  .step-content p {
    font-size: 0.95rem;
    line-height: 1.65;
    margin-bottom: var(--spacing-md);
  }

  .section-block ul li,
  .step-content ul li {
    font-size: 0.9rem;
    line-height: 1.6;
    margin-bottom: var(--spacing-sm);
  }

  /* Mobile Spacing */
  .section-block {
    padding: var(--spacing-md);
    margin-bottom: var(--spacing-lg);
  }

  .prep-step {
    padding: var(--spacing-lg);
    margin-bottom: var(--spacing-lg);
  }

  .step-header {
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-md);
  }

  .step-number {
    width: 40px;
    height: 40px;
    font-size: 1.25rem;
  }

  .step-icon {
    display: none;
  }

  .step-content {
    padding: 0;
  }

  /* Mobile Hero Accent */
  .hero-accent {
    flex-direction: column;
    gap: var(--spacing-sm);
    padding: var(--spacing-md);
  }

  .accent-divider {
    display: none;
  }

  /* Mobile Hero Bullets */
  .hero-bullets {
    max-width: 100%;
    gap: var(--spacing-md);
  }

  .hero-bullet {
    font-size: 0.9rem;
    line-height: 1.6;
    padding: var(--spacing-sm);
  }

  /* Mobile Paragraphs - Break Long Text */
  .section-subtitle {
    font-size: 0.95rem;
    line-height: 1.65;
  }

  /* Mobile Screen Pause */
  .pause-text {
    font-size: clamp(1.25rem, 4vw, 1.75rem);
    padding: 0 var(--spacing-md);
  }

  .timeline::before {
    left: 20px;
  }

  .timeline-marker {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }

  .timeline-item {
    gap: var(--spacing-sm);
  }

  .cards-grid {
    grid-template-columns: 1fr;
  }

  .rhythm-grid {
    grid-template-columns: 1fr;
  }

  .footer-actions {
    flex-direction: column;
    align-items: stretch;
  }

  .cta-button.large {
    width: 100%;
    justify-content: center;
  }

  .toast {
    left: var(--spacing-sm);
    right: var(--spacing-sm);
  }

  .prompt-actions {
    flex-direction: column;
  }

  .cta-button.secondary {
    width: 100%;
    justify-content: center;
  }

  /* Mobile Checklists */
  .checklists-grid {
    grid-template-columns: 1fr;
  }

  .criteria-grid {
    grid-template-columns: 1fr;
  }
}

/* ===========================
     NEXT STEP BLOCK
     =========================== */

.next-step-block {
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.15) 0%,
    rgba(139, 92, 246, 0.15) 50%,
    rgba(236, 72, 153, 0.15) 100%
  );
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-lg);
  padding: var(--spacing-xl);
  margin: var(--spacing-xl) 0;
  display: flex;
  gap: var(--spacing-lg);
  align-items: flex-start;
  backdrop-filter: blur(10px);
}

.next-step-icon {
  flex-shrink: 0;
  width: 60px;
  height: 60px;
  background: var(--gradient-primary);
  border-radius: var(--radius-md);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  box-shadow: 0 4px 20px rgba(99, 102, 241, 0.3);
}

.next-step-content h3 {
  font-size: 1.5rem;
  margin: 0 0 var(--spacing-sm);
  color: var(--text-primary);
}

.next-step-content p {
  margin: 0 0 var(--spacing-md);
  color: var(--text-secondary);
  line-height: 1.6;
}

.next-step-content .cta-button {
  margin-top: var(--spacing-md);
}

/* ===========================
     PYRAMID GLASS VISUALIZATION
     =========================== */

.pyramid-glass-container {
  margin: var(--spacing-xl) 0;
  padding: var(--spacing-xl);
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: var(--radius-lg);
  display: flex;
  justify-content: center;
  align-items: center;
}

.pyramid-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  gap: var(--spacing-xl);
  width: 100%;
  max-width: 1200px;
}

.pyramid-glass-svg {
  flex: 1;
  max-width: 700px;
  height: auto;
  filter: drop-shadow(0 10px 40px rgba(99, 102, 241, 0.4));
}

.glass-shape {
  transition: all 0.3s ease;
  cursor: pointer;
}

.glass-shape:hover {
  stroke: rgba(99, 102, 241, 1);
  stroke-width: 3;
}

.pyramid-text {
  pointer-events: none;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

.pyramid-glass-level:hover .glass-shape {
  stroke: rgba(99, 102, 241, 1);
  stroke-width: 3;
}

.pyramid-glass-level:hover .pyramid-text {
  font-size: 20px;
}

/* Hover description block */
.pyramid-hover-description {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 320px;
  padding: var(--spacing-lg);
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.15) 0%,
    rgba(139, 92, 246, 0.15) 100%
  );
  border: 1px solid rgba(99, 102, 241, 0.4);
  border-radius: var(--radius-lg);
  backdrop-filter: blur(20px);
  box-shadow: 0 8px 32px rgba(99, 102, 241, 0.3);
  animation: slideInRight 0.3s ease;
  z-index: 10;
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateY(-50%) translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateY(-50%) translateX(0);
  }
}

.hover-desc-number {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.25rem;
  color: white;
  margin-bottom: var(--spacing-md);
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.4);
}

.pyramid-hover-description h4 {
  margin: 0 0 var(--spacing-sm);
  font-size: 1.25rem;
  color: var(--primary);
  font-weight: 700;
  line-height: 1.3;
}

.pyramid-hover-description p {
  margin: 0;
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-secondary);
}

/* ===========================
     READINESS CHECKLISTS
     =========================== */

.readiness-checklists {
  margin: var(--spacing-xl) 0;
}

.readiness-checklists > h3 {
  font-size: 1.75rem;
  margin-bottom: var(--spacing-lg);
  text-align: center;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.checklists-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-lg);
  margin-top: var(--spacing-lg);
}

/* SS Readiness Criteria */
.ss-criteria {
  margin: var(--spacing-2xl) 0;
  padding: var(--spacing-xl);
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.1) 0%,
    rgba(139, 92, 246, 0.1) 100%
  );
  border: 2px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-lg);
  backdrop-filter: blur(10px);
}

.ss-criteria > h3 {
  font-size: 1.5rem;
  margin-bottom: var(--spacing-lg);
  text-align: center;
  color: var(--primary);
  font-weight: 700;
}

.criteria-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--spacing-md);
}

.criteria-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-md);
  background: rgba(255, 255, 255, 0.05);
  border-radius: var(--radius-md);
  font-weight: 500;
  color: var(--text-primary);
  transition: all 0.3s ease;
}

.criteria-item:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: translateX(4px);
}

.criteria-check {
  flex-shrink: 0;
  width: 28px;
  height: 28px;
  background: var(--gradient-primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.1rem;
  color: white;
}

.checklist-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.checklist-card:hover {
  transform: translateY(-4px);
  border-color: var(--primary);
  box-shadow: 0 8px 30px rgba(99, 102, 241, 0.2);
}

.checklist-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-md);
  padding-bottom: var(--spacing-md);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.checklist-number {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  background: var(--gradient-primary);
  border-radius: var(--radius-sm);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.1rem;
  color: white;
}

.checklist-header h4 {
  margin: 0;
  font-size: 1.1rem;
  color: var(--text-primary);
}

.checklist-items {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.checklist-items li {
  display: flex;
  gap: var(--spacing-sm);
  align-items: flex-start;
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 0.95rem;
}

.checklist-items li svg {
  flex-shrink: 0;
  margin-top: 3px;
  color: var(--primary);
}

.checklist-bring {
  margin-top: var(--spacing-md);
  padding: var(--spacing-sm) var(--spacing-md);
  background: rgba(99, 102, 241, 0.1);
  border-left: 3px solid var(--primary);
  border-radius: var(--radius-sm);
  font-size: 0.85rem;
  color: var(--text-secondary);
}

.checklist-bring strong {
  color: var(--primary);
  font-weight: 600;
}

.checklist-items li span {
  flex: 1;
}

/* ===========================
     PREP TABS
     =========================== */

/* ===========================
     PREP STEPS (Vertical Flow)
     =========================== */

.prep-steps {
  margin: var(--spacing-2xl) 0;
}

.steps-title {
  font-size: 2rem;
  text-align: center;
  margin-bottom: var(--spacing-2xl);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.prep-step {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-xl);
  margin-bottom: var(--spacing-xl);
  transition: all 0.3s ease;
}

.prep-step:hover {
  border-color: var(--primary);
  transform: translateY(-4px);
  box-shadow: 0 8px 30px rgba(99, 102, 241, 0.2);
}

.prep-step.highlight {
  border-color: rgba(99, 102, 241, 0.4);
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.05) 0%,
    rgba(139, 92, 246, 0.05) 100%
  );
}

.step-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);
  padding-bottom: var(--spacing-lg);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.step-number {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  background: var(--gradient-primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  font-size: 1.5rem;
  color: white;
  box-shadow: 0 4px 15px rgba(99, 102, 241, 0.4);
}

.step-icon {
  flex-shrink: 0;
  color: var(--primary);
}

.step-title-block {
  flex: 1;
}

.step-title-block h4 {
  font-size: 1.5rem;
  margin: 0 0 var(--spacing-xs) 0;
  color: var(--text-primary);
}

.step-outcome {
  font-size: 0.95rem;
  color: var(--primary);
  font-weight: 500;
  margin: 0;
}

.step-content {
  padding: 0 var(--spacing-lg);
}

.step-description {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: var(--spacing-md);
}

.step-content p {
  color: var(--text-secondary);
  line-height: 1.7;
  margin-bottom: var(--spacing-md);
}

.step-content ul {
  margin: var(--spacing-md) 0;
}

.step-content ul li {
  margin-bottom: var(--spacing-sm);
  color: var(--text-secondary);
}

.step-content .cta-button {
  margin-top: var(--spacing-lg);
}

/* Meeting Items in Steps */
.step-content .meeting-item {
  display: flex;
  gap: var(--spacing-md);
  align-items: flex-start;
  padding: var(--spacing-md);
  background: rgba(255, 255, 255, 0.02);
  border-radius: var(--radius-md);
  margin-bottom: var(--spacing-md);
  border-left: 3px solid var(--primary);
}

.step-content .meeting-number {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  background: var(--gradient-primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: white;
}

.step-content .meeting-content {
  flex: 1;
}

.step-content .meeting-content strong {
  display: block;
  color: var(--text-primary);
  margin-bottom: var(--spacing-xs);
}

.step-content .meeting-content p {
  margin: 0;
  color: var(--text-secondary);
}

/* AI Features in Step */
.ai-features,
.ai-result {
  margin: var(--spacing-lg) 0;
}

.ai-features h5,
.ai-result h5 {
  color: var(--text-primary);
  margin-bottom: var(--spacing-sm);
}

/* ===========================
     SCREEN PAUSES
     =========================== */

.screen-pause {
  padding: calc(var(--spacing-2xl) * 2) 0;
  text-align: center;
  background: radial-gradient(
    circle at 50% 50%,
    rgba(99, 102, 241, 0.08) 0%,
    transparent 70%
  );
}

.pause-text {
  font-size: clamp(1.5rem, 3vw, 2.5rem);
  font-weight: 600;
  line-height: 1.4;
  color: var(--text-primary);
  max-width: 900px;
  margin: 0 auto;
  letter-spacing: -0.02em;
}

/* ===========================
     IMPROVED PROMPT BOX
     =========================== */

.prompt-box {
  background: rgba(15, 15, 30, 0.8);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-md);
  padding: var(--spacing-md);
  margin: var(--spacing-md) 0;
  max-height: 200px;
  overflow-y: auto;
  position: relative;
  transition: max-height 0.4s ease;
}

.prompt-box.expanded {
  max-height: 600px;
}

.prompt-box.collapsed {
  max-height: 200px;
}

.prompt-box pre {
  margin: 0;
  font-family: "Courier New", monospace;
  font-size: 0.85rem;
  line-height: 1.6;
  color: var(--text-secondary);
  white-space: pre-wrap;
  word-wrap: break-word;
}

.expand-button {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  margin: var(--spacing-sm) 0;
  padding: var(--spacing-xs) var(--spacing-md);
  background: rgba(99, 102, 241, 0.1);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-sm);
  color: var(--primary);
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.expand-button:hover {
  background: rgba(99, 102, 241, 0.2);
  border-color: var(--primary);
  transform: translateY(-1px);
}

.expand-button svg {
  flex-shrink: 0;
}

.prompt-actions {
  display: flex;
  gap: var(--spacing-sm);
  margin-top: var(--spacing-md);
  flex-wrap: wrap;
}

@media (max-width: 480px) {
  .hero-bullets {
    text-align: left;
  }

  .section-header h2 {
    font-size: 1.75rem;
  }

  .tool-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .meeting-item {
    flex-direction: column;
  }

  .next-step-block {
    flex-direction: column;
    padding: var(--spacing-lg);
  }

  .next-step-icon {
    width: 50px;
    height: 50px;
  }

  .checklists-grid {
    grid-template-columns: 1fr;
  }

  .tabs-header {
    gap: var(--spacing-xs);
  }

  .tab-button {
    padding: var(--spacing-sm) var(--spacing-md);
    font-size: 0.9rem;
  }

  .tab-button span {
    display: none;
  }

  .prompt-box {
    max-height: 150px;
  }

  .prompt-box.expanded {
    max-height: 400px;
  }

  .prompt-actions {
    flex-direction: column;
  }

  .prompt-actions button {
    width: 100%;
    justify-content: center;
  }

  /* Pyramid mobile */
  .pyramid-wrapper {
    flex-direction: column;
    gap: var(--spacing-lg);
  }

  .pyramid-glass-svg {
    max-width: 100%;
  }

  .pyramid-hover-description {
    position: static;
    transform: none;
    width: 100%;
    margin-top: var(--spacing-md);
    animation: fadeIn 0.3s ease;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
}

/* ===========================
     MOBILE STICKY CTA
     =========================== */

.mobile-sticky-cta {
  display: none;
}

@media (max-width: 768px) {
  .mobile-sticky-cta {
    display: block;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(
      180deg,
      transparent 0%,
      rgba(10, 10, 20, 0.95) 20%,
      rgba(10, 10, 20, 0.98) 100%
    );
    padding: var(--spacing-sm) var(--spacing-md) var(--spacing-md);
    backdrop-filter: blur(10px);
    border-top: 1px solid rgba(99, 102, 241, 0.3);
    z-index: 1000;
    box-shadow: 0 -4px 20px rgba(0, 0, 0, 0.3);
    transform: translateY(0);
    transition: transform 0.3s ease, opacity 0.3s ease;
    opacity: 1;
  }

  .mobile-sticky-cta.hidden {
    transform: translateY(100%);
    opacity: 0;
    pointer-events: none;
  }

  .mobile-sticky-cta.visible {
    transform: translateY(0);
    opacity: 1;
  }

  .sticky-cta-button {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--spacing-sm);
    padding: var(--spacing-md) var(--spacing-lg);
    background: var(--gradient-primary);
    border: none;
    border-radius: var(--radius-lg);
    color: white;
    font-weight: 600;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(99, 102, 241, 0.4);
  }

  .sticky-cta-button:active {
    transform: scale(0.98);
    box-shadow: 0 2px 10px rgba(99, 102, 241, 0.3);
  }

  .sticky-cta-button svg {
    flex-shrink: 0;
  }

  /*  padding-bottom  body   */
  body {
    padding-bottom: 80px;
  }
}

/* ===========================
     PROGRAM CALENDAR
     =========================== */

.program-calendar {
  margin: var(--spacing-2xl) 0;
}

.calendar-header {
  display: flex;
  align-items: flex-start;
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-2xl);
}

.calendar-icon {
  flex-shrink: 0;
  color: var(--primary);
}

.calendar-header h2 {
  font-size: 2rem;
  margin: 0 0 var(--spacing-sm) 0;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.calendar-note {
  font-size: 0.9rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
}

/* Timeline */
.calendar-timeline {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-2xl);
}

.timeline-month {
  position: relative;
}

.month-header {
  margin-bottom: var(--spacing-lg);
  padding-left: var(--spacing-lg);
  border-left: 3px solid var(--primary);
}

.month-label {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  text-transform: capitalize;
}

.month-events {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: var(--spacing-lg);
  padding-left: var(--spacing-xl);
}

/* Event Cards */
.event-card {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.event-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  transition: all 0.3s ease;
}

.event-card.level-nechto::before {
  background: rgba(156, 163, 175, 0.5);
}

.event-card.level-ultima::before {
  background: var(--gradient-primary);
}

.event-card.level-group::before {
  background: linear-gradient(
    135deg,
    rgba(236, 72, 153, 0.8) 0%,
    rgba(219, 39, 119, 0.8) 100%
  );
}

.event-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 30px rgba(99, 102, 241, 0.2);
}

.event-card.level-ultima:hover {
  border-color: var(--primary);
}

.event-card.level-group:hover {
  border-color: rgba(236, 72, 153, 0.6);
}

.event-card-header {
  margin-bottom: var(--spacing-md);
}

.event-badges {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-xs);
}

.badge {
  display: inline-block;
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--radius-sm);
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.badge-level.level-nechto {
  background: rgba(156, 163, 175, 0.2);
  color: rgba(156, 163, 175, 1);
  border: 1px solid rgba(156, 163, 175, 0.3);
}

.badge-level.level-ultima {
  background: rgba(99, 102, 241, 0.2);
  color: var(--primary);
  border: 1px solid rgba(99, 102, 241, 0.3);
}

.badge-level.level-group {
  background: rgba(236, 72, 153, 0.2);
  color: rgb(236, 72, 153);
  border: 1px solid rgba(236, 72, 153, 0.3);
}

.badge-format {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-secondary);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.badge-status {
  background: rgba(251, 191, 36, 0.2);
  color: rgb(251, 191, 36);
  border: 1px solid rgba(251, 191, 36, 0.3);
}

.event-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: var(--spacing-sm) 0;
  line-height: 1.4;
}

.event-meta {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
  margin: var(--spacing-md) 0;
  font-size: 0.85rem;
  color: var(--text-secondary);
}

.meta-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
}

.meta-item svg {
  flex-shrink: 0;
  opacity: 0.7;
}

.event-details-btn {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  padding: var(--spacing-sm) 0;
  background: none;
  border: none;
  color: var(--primary);
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: var(--spacing-md);
}

.event-details-btn:hover {
  gap: var(--spacing-sm);
  opacity: 0.8;
}

/* Event Modal */
.event-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  padding: var(--spacing-lg);
  animation: fadeIn 0.3s ease;
}

.event-modal {
  background: linear-gradient(
    135deg,
    rgba(20, 20, 40, 0.95) 0%,
    rgba(30, 30, 50, 0.95) 100%
  );
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-lg);
  max-width: 600px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal-close {
  position: absolute;
  top: var(--spacing-lg);
  right: var(--spacing-lg);
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.3s ease;
}

.modal-close:hover {
  background: rgba(255, 255, 255, 0.2);
  color: var(--text-primary);
}

.modal-header {
  padding: var(--spacing-2xl) var(--spacing-xl) var(--spacing-lg);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.modal-header h3 {
  font-size: 1.75rem;
  margin: 0 0 var(--spacing-md) 0;
  padding-right: var(--spacing-2xl);
  color: var(--text-primary);
}

.modal-badges {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-xs);
}

.modal-body {
  padding: var(--spacing-xl);
}

.modal-section {
  margin-bottom: var(--spacing-lg);
  padding-bottom: var(--spacing-lg);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.modal-section:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.modal-section.highlight {
  background: rgba(99, 102, 241, 0.1);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-md);
  padding: var(--spacing-md);
  border-bottom: 1px solid rgba(99, 102, 241, 0.3);
}

.section-label {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-sm);
  color: var(--primary);
  font-size: 0.95rem;
}

.section-label svg {
  flex-shrink: 0;
}

.modal-section p {
  color: var(--text-secondary);
  line-height: 1.7;
  margin: 0;
}

.modal-note {
  background: rgba(251, 191, 36, 0.1);
  border: 1px solid rgba(251, 191, 36, 0.3);
  border-radius: var(--radius-md);
  padding: var(--spacing-md);
  color: rgb(251, 191, 36);
  font-size: 0.9rem;
  text-align: center;
  margin-top: var(--spacing-lg);
}

/* Mobile Calendar */
@media (max-width: 768px) {
  .calendar-header {
    flex-direction: column;
    gap: var(--spacing-md);
  }

  .month-events {
    grid-template-columns: 1fr;
    padding-left: var(--spacing-md);
  }

  .event-card {
    padding: var(--spacing-md);
  }

  .event-title {
    font-size: 1rem;
  }

  .event-modal {
    max-height: 85vh;
    margin: var(--spacing-sm);
  }

  .modal-header h3 {
    font-size: 1.35rem;
  }

  .modal-body {
    padding: var(--spacing-lg) var(--spacing-md);
  }
}

/* ===========================
     CALENDAR CARD (ONBOARDING)
     =========================== */

.calendar-card {
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.1) 0%,
    rgba(139, 92, 246, 0.1) 50%,
    rgba(236, 72, 153, 0.1) 100%
  );
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-lg);
  padding: var(--spacing-2xl);
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: var(--spacing-xl);
  position: relative;
  overflow: hidden;
}

.calendar-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: var(--gradient-primary);
}

.calendar-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(99, 102, 241, 0.3);
  border-color: var(--primary);
}

.calendar-card-icon {
  flex-shrink: 0;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(99, 102, 241, 0.2);
  border-radius: var(--radius-md);
  color: var(--primary);
}

.calendar-card-content {
  flex: 1;
}

.calendar-card-content h3 {
  font-size: 1.75rem;
  margin: 0 0 var(--spacing-sm) 0;
  color: var(--text-primary);
}

.calendar-card-content p {
  font-size: 1rem;
  color: var(--text-secondary);
  margin: 0;
}

.calendar-card-subtitle {
  font-size: 0.9rem !important;
  color: var(--text-muted) !important;
  margin-top: var(--spacing-xs) !important;
}

.calendar-card-action {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  font-weight: 600;
  color: var(--primary);
  transition: all 0.3s ease;
}

.calendar-card:hover .calendar-card-action {
  gap: var(--spacing-md);
}

/* ===========================
     CALENDAR OVERLAY
     =========================== */

.calendar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(10, 10, 20, 0.98);
  backdrop-filter: blur(10px);
  z-index: 3000;
  display: flex;
  flex-direction: column;
  animation: fadeIn 0.3s ease;
}

.calendar-overlay-header {
  position: sticky;
  top: 0;
  background: linear-gradient(
    180deg,
    rgba(10, 10, 20, 1) 0%,
    rgba(10, 10, 20, 0.95) 100%
  );
  border-bottom: 1px solid rgba(99, 102, 241, 0.3);
  padding: var(--spacing-xl) 0;
  z-index: 10;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.calendar-overlay-header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--spacing-xl);
}

.overlay-header-content h2 {
  font-size: 2.25rem;
  margin: 0 0 var(--spacing-xs) 0;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.overlay-subtitle {
  font-size: 1rem;
  color: var(--text-secondary);
  margin: 0;
}

.overlay-close-btn {
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.3s ease;
}

.overlay-close-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: var(--text-primary);
  transform: rotate(90deg);
}

.calendar-overlay-body {
  flex: 1;
  overflow-y: auto;
  padding: var(--spacing-2xl) 0;
}

/*     overlay  */
.calendar-overlay .calendar-header {
  display: none;
}

/* Mobile Overlay */
@media (max-width: 768px) {
  .calendar-card {
    flex-direction: column;
    text-align: center;
    padding: var(--spacing-xl);
  }

  .calendar-card-icon {
    width: 64px;
    height: 64px;
  }

  .calendar-card-content h3 {
    font-size: 1.5rem;
  }

  .calendar-card-action {
    justify-content: center;
  }

  .calendar-overlay-header {
    padding: var(--spacing-lg) 0;
  }

  .overlay-header-content h2 {
    font-size: 1.75rem;
  }

  .overlay-subtitle {
    font-size: 0.9rem;
  }

  .overlay-close-btn {
    width: 40px;
    height: 40px;
  }

  .calendar-overlay-body {
    padding: var(--spacing-xl) 0;
  }
}

/* ===========================
     CALENDAR MODE TOGGLE
     =========================== */

.calendar-mode-toggle {
  display: flex;
  gap: 0;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-full);
  padding: 4px;
  backdrop-filter: blur(10px);
}

.mode-btn {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--radius-full);
  background: transparent;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  font-weight: 500;
  white-space: nowrap;
}

.mode-btn:hover {
  color: var(--text-primary);
  background: rgba(255, 255, 255, 0.05);
}

.mode-btn.active {
  background: var(--gradient-primary);
  color: white;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.3);
}

.overlay-header-actions {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
}

/* ===========================
     CALENDAR GRID
     =========================== */

.calendar-grid-container {
  width: 100%;
  animation: fadeIn 0.5s ease;
}

.calendar-grid-year {
  text-align: center;
  margin-bottom: var(--spacing-2xl);
}

.calendar-grid-year h2 {
  font-size: 3rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-2xl);
}

/* Month Card */
.month-card {
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.05) 0%,
    rgba(255, 255, 255, 0.02) 100%
  );
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  position: relative;
}

.month-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(99, 102, 241, 0.2);
  border-color: rgba(99, 102, 241, 0.3);
}

.month-header {
  margin-bottom: var(--spacing-md);
  text-align: center;
  padding-bottom: var(--spacing-sm);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.month-header h3 {
  font-size: 1.25rem;
  margin: 0;
  background: linear-gradient(
    135deg,
    var(--text-primary),
    var(--text-secondary)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.month-weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 2px;
  margin-bottom: var(--spacing-xs);
}

.weekday-label {
  text-align: center;
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--text-muted);
  padding: 4px 0;
}

.month-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 2px;
}

/* Day Cell */
.day-cell {
  aspect-ratio: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-sm);
  background: rgba(255, 255, 255, 0.02);
  position: relative;
  transition: all 0.2s ease;
  cursor: default;
}

.day-cell.empty {
  background: transparent;
}

.day-cell.has-events {
  cursor: pointer;
  background: rgba(99, 102, 241, 0.1);
  border: 1px solid rgba(99, 102, 241, 0.2);
}

.day-cell.has-events:hover {
  background: rgba(99, 102, 241, 0.2);
  transform: scale(1.05);
}

.day-cell.today {
  border: 2px solid var(--primary);
  font-weight: 700;
}

.day-number {
  font-size: 0.75rem;
  color: var(--text-secondary);
  z-index: 2;
  position: relative;
}

.day-cell.has-events .day-number {
  color: var(--text-primary);
}

.day-markers {
  display: flex;
  gap: 2px;
  margin-top: 2px;
  z-index: 2;
  position: relative;
}

.event-dot {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  flex-shrink: 0;
}

.more-count {
  font-size: 0.6rem;
  color: var(--text-muted);
  font-weight: 600;
}

/* Range Highlight */
.range-highlight {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.2;
  z-index: 1;
  pointer-events: none;
}

.range-highlight.start {
  border-radius: var(--radius-sm) 0 0 var(--radius-sm);
}

.range-highlight.end {
  border-radius: 0 var(--radius-sm) var(--radius-sm) 0;
}

.range-highlight.mid {
  border-radius: 0;
}

.range-highlight.start.end {
  border-radius: var(--radius-sm);
}

/* Event Popup (Grid Mode) */
.event-popup {
  position: fixed;
  background: linear-gradient(
    135deg,
    rgba(20, 20, 40, 0.98) 0%,
    rgba(30, 30, 50, 0.98) 100%
  );
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: var(--radius-lg);
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(20px);
  max-width: 400px;
  z-index: 4000;
  animation: popupFadeIn 0.2s ease;
  transform: translate(-50%, 0);
}

@keyframes popupFadeIn {
  from {
    opacity: 0;
    transform: translate(-50%, -8px);
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

.popup-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--spacing-md) var(--spacing-lg);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.popup-header h4 {
  margin: 0;
  font-size: 1.1rem;
  color: var(--text-primary);
}

.popup-close {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 50%;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s ease;
}

.popup-close:hover {
  background: rgba(255, 255, 255, 0.2);
  color: var(--text-primary);
}

.popup-badges {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-xs);
  padding: var(--spacing-md) var(--spacing-lg);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.badge-nechto {
  background: rgba(156, 163, 175, 0.2);
  color: rgb(156, 163, 175);
  border-color: rgba(156, 163, 175, 0.3);
}

.badge-ultima {
  background: rgba(99, 102, 241, 0.2);
  color: rgb(99, 102, 241);
  border-color: rgba(99, 102, 241, 0.3);
}

.badge-group {
  background: rgba(236, 72, 153, 0.2);
  color: rgb(236, 72, 153);
  border-color: rgba(236, 72, 153, 0.3);
}

.popup-body {
  padding: var(--spacing-lg);
  max-height: 60vh;
  overflow-y: auto;
}

.popup-section {
  margin-bottom: var(--spacing-md);
}

.popup-section:last-child {
  margin-bottom: 0;
}

.popup-section p {
  margin: 0;
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 0.9rem;
}

/* Multi-events popup */
.event-list-item {
  padding: var(--spacing-sm) 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  cursor: pointer;
  transition: all 0.2s ease;
}

.event-list-item:hover {
  background: rgba(255, 255, 255, 0.05);
  padding-left: var(--spacing-sm);
  padding-right: var(--spacing-sm);
  border-radius: var(--radius-sm);
}

.event-list-item:last-child {
  border-bottom: none;
}

.event-list-badges {
  margin-bottom: 4px;
}

.event-list-content h5 {
  margin: 0 0 4px 0;
  font-size: 0.95rem;
  color: var(--text-primary);
}

.event-time {
  font-size: 0.8rem;
  color: var(--text-muted);
}

/* Calendar Legend */
.calendar-legend {
  display: flex;
  justify-content: center;
  gap: var(--spacing-xl);
  padding: var(--spacing-lg);
  background: rgba(255, 255, 255, 0.03);
  border-radius: var(--radius-md);
}

.legend-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.legend-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.legend-dot.nechto {
  background: rgba(156, 163, 175, 0.8);
}

.legend-dot.ultima {
  background: rgba(99, 102, 241, 0.8);
}

.legend-dot.group {
  background: rgba(236, 72, 153, 0.8);
}

/* ===========================
     CALENDAR GRID - RESPONSIVE
     =========================== */

/* Tablet */
@media (max-width: 1024px) {
  .calendar-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .calendar-grid-year h2 {
    font-size: 2.5rem;
  }
}

/* Mobile */
@media (max-width: 768px) {
  .calendar-mode-toggle {
    flex-direction: column;
    width: 100%;
  }

  .mode-btn {
    width: 100%;
    justify-content: center;
  }

  .overlay-header-actions {
    flex-direction: column;
    width: 100%;
    gap: var(--spacing-sm);
  }

  .overlay-close-btn {
    position: absolute;
    top: var(--spacing-md);
    right: var(--spacing-md);
  }

  .calendar-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-md);
  }

  .calendar-grid-year h2 {
    font-size: 2rem;
  }

  .month-card {
    padding: var(--spacing-md);
  }

  .month-header h3 {
    font-size: 1.1rem;
  }

  .weekday-label {
    font-size: 0.65rem;
  }

  .day-number {
    font-size: 0.7rem;
  }

  .event-dot {
    width: 3px;
    height: 3px;
  }

  .event-popup {
    max-width: 90vw;
    left: 50% !important;
    top: 50% !important;
    transform: translate(-50%, -50%) !important;
  }

  .calendar-legend {
    flex-wrap: wrap;
    gap: var(--spacing-md);
  }
}

/* ===========================
   ONBOARDING SECTION STYLES
   =========================== */

.onboarding-section {
  min-height: 100vh;
  padding: var(--spacing-2xl) 0;
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.05),
    rgba(139, 92, 246, 0.05)
  );
}

/* Progress Card */
.onboarding-progress-card {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-xl);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-xl);
}

.progress-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-md);
}

.progress-info {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
}

.progress-label {
  font-size: 1.1rem;
  font-weight: 500;
}

.progress-value {
  font-size: 1.5rem;
  font-weight: 700;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.progress-count {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.reset-button {
  background: transparent;
  border: 1px solid var(--border);
  color: var(--text-secondary);
  padding: var(--spacing-xs);
  border-radius: var(--radius-sm);
  cursor: pointer;
  transition: all 0.3s;
}

.reset-button:hover {
  border-color: var(--border-hover);
  color: var(--text-primary);
  transform: rotate(180deg);
}

.progress-bar-container {
  width: 100%;
  height: 12px;
  background: var(--bg-tertiary);
  border-radius: 999px;
  overflow: hidden;
  position: relative;
}

.progress-bar-fill {
  height: 100%;
  background: var(--gradient-primary);
  border-radius: 999px;
  transition: width 0.5s ease;
  box-shadow: 0 0 20px rgba(99, 102, 241, 0.5);
}

/* Content Wrapper */
.onboarding-content-wrapper {
  display: grid;
  grid-template-columns: 250px 1fr 300px;
  gap: var(--spacing-xl);
  margin-top: var(--spacing-xl);
}

/* Navigation Sidebar */
.onboarding-nav {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-xl);
  padding: var(--spacing-lg);
  height: fit-content;
  position: sticky;
  top: 100px;
}

.onboarding-nav h3 {
  font-size: 1rem;
  margin-bottom: var(--spacing-md);
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.onboarding-sections-list {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
}

.section-tab {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm);
  background: transparent;
  border: 1px solid transparent;
  border-radius: var(--radius-md);
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.3s;
  text-align: left;
  font-size: 0.9rem;
}

.section-tab:hover {
  background: rgba(99, 102, 241, 0.1);
  border-color: var(--border-hover);
  color: var(--text-primary);
  transform: translateX(4px);
}

.section-tab.active {
  background: var(--gradient-primary);
  border-color: transparent;
  color: white;
  transform: translateX(0);
}

.section-icon {
  flex-shrink: 0;
  display: flex;
  align-items: center;
}

.section-title {
  flex: 1;
}

/* Main Content Area */
.onboarding-main-content {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-xl);
  padding: var(--spacing-xl);
}

.section-content-header h3 {
  font-size: 1.8rem;
  margin-bottom: var(--spacing-sm);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.section-subtitle {
  color: var(--text-secondary);
  font-size: 1.1rem;
  margin-bottom: var(--spacing-lg);
}

.section-content-body {
  color: var(--text-secondary);
  line-height: 1.8;
}

.content-text {
  margin-bottom: var(--spacing-lg);
}

.content-text p {
  font-size: 1.05rem;
}

/* Documents List */
.content-documents {
  margin: var(--spacing-xl) 0;
}

.content-documents h4 {
  color: var(--text-primary);
  margin-bottom: var(--spacing-md);
  font-size: 1.1rem;
}

.documents-list {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.document-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-md);
  background: rgba(99, 102, 241, 0.1);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  color: var(--text-primary);
  text-decoration: none;
  transition: all 0.3s;
}

.document-item:hover {
  background: rgba(99, 102, 241, 0.2);
  border-color: var(--primary);
  transform: translateX(4px);
}

/* Stages */
.content-stages {
  margin: var(--spacing-xl) 0;
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
}

.stage-item {
  display: flex;
  gap: var(--spacing-md);
  padding: var(--spacing-lg);
  background: rgba(139, 92, 246, 0.05);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  transition: all 0.3s;
}

.stage-item:hover {
  background: rgba(139, 92, 246, 0.1);
  border-color: var(--border-hover);
  transform: translateY(-2px);
}

.stage-number {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gradient-primary);
  color: white;
  border-radius: 50%;
  font-weight: 700;
  font-size: 1.1rem;
}

.stage-content h5 {
  color: var(--text-primary);
  margin-bottom: var(--spacing-xs);
  font-size: 1.1rem;
}

.stage-content p {
  color: var(--text-secondary);
  font-size: 0.95rem;
}

/* Items List */
.content-items ul {
  list-style: none;
  padding-left: 0;
}

.content-items li {
  padding: var(--spacing-sm) var(--spacing-md);
  margin-bottom: var(--spacing-sm);
  background: rgba(99, 102, 241, 0.05);
  border-left: 3px solid var(--primary);
  border-radius: var(--radius-sm);
}

/* Modal Trigger Button */
.modal-trigger-button {
  margin-top: var(--spacing-lg);
  padding: var(--spacing-md) var(--spacing-lg);
  background: var(--gradient-primary);
  color: white;
  border: none;
  border-radius: var(--radius-md);
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s;
}

.modal-trigger-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(99, 102, 241, 0.4);
}

/* Section Navigation */
.section-navigation {
  display: flex;
  justify-content: space-between;
  gap: var(--spacing-md);
  margin-top: var(--spacing-xl);
  padding-top: var(--spacing-lg);
  border-top: 1px solid var(--border);
}

.nav-button {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-md) var(--spacing-lg);
  background: rgba(99, 102, 241, 0.1);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.3s;
  font-size: 0.95rem;
}

.nav-button:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.nav-button:not(:disabled):hover {
  background: var(--gradient-primary);
  border-color: transparent;
  transform: translateY(-2px);
}

/* Checklist Sidebar */
.onboarding-checklist {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: var(--radius-xl);
  padding: var(--spacing-lg);
  height: fit-content;
  position: sticky;
  top: 100px;
}

.onboarding-checklist h3 {
  font-size: 1.1rem;
  margin-bottom: var(--spacing-md);
  color: var(--text-primary);
}

.checklist-items {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.checklist-item {
  display: flex;
  align-items: flex-start;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm);
  cursor: pointer;
  transition: all 0.3s;
  border-radius: var(--radius-sm);
}

.checklist-item:hover {
  background: rgba(99, 102, 241, 0.05);
}

.checklist-item input[type="checkbox"] {
  margin-top: 4px;
  cursor: pointer;
  width: 18px;
  height: 18px;
  accent-color: var(--primary);
}

.checklist-item span {
  flex: 1;
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.5;
}

.checklist-item span.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  padding: var(--spacing-xl);
}

.modal-content {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  border-radius: var(--radius-xl);
  padding: var(--spacing-xl);
  max-width: 800px;
  width: 100%;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
}

.modal-close {
  position: absolute;
  top: var(--spacing-lg);
  right: var(--spacing-lg);
  background: transparent;
  border: 1px solid var(--border);
  color: var(--text-secondary);
  padding: var(--spacing-xs);
  border-radius: var(--radius-sm);
  cursor: pointer;
  transition: all 0.3s;
}

.modal-close:hover {
  border-color: var(--border-hover);
  color: var(--text-primary);
}

.modal-content h3 {
  font-size: 1.8rem;
  margin-bottom: var(--spacing-lg);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.modal-body {
  color: var(--text-secondary);
  line-height: 1.8;
}

.modal-body h4 {
  color: var(--text-primary);
  margin-top: var(--spacing-lg);
  margin-bottom: var(--spacing-md);
}

.modal-body ul {
  list-style: none;
  padding-left: 0;
}

.modal-body li {
  padding: var(--spacing-xs) 0;
  padding-left: var(--spacing-lg);
  position: relative;
}

.modal-body li::before {
  content: "";
  position: absolute;
  left: 0;
  color: var(--primary);
}

.modal-body p {
  margin-bottom: var(--spacing-md);
}

.modal-body pre {
  background: var(--bg-tertiary);
  padding: var(--spacing-md);
  border-radius: var(--radius-md);
  border: 1px solid var(--border);
  overflow-x: auto;
  margin: var(--spacing-md) 0;
}

/* Mobile Responsive */
@media (max-width: 1024px) {
  .onboarding-content-wrapper {
    grid-template-columns: 1fr;
    gap: var(--spacing-lg);
  }

  .onboarding-nav,
  .onboarding-checklist {
    position: static;
  }

  .onboarding-nav {
    order: -1;
  }

  .onboarding-sections-list {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .section-tab {
    flex: 1 1 auto;
    min-width: 150px;
  }
}

@media (max-width: 768px) {
  .onboarding-section {
    padding: var(--spacing-lg) 0;
  }

  .onboarding-progress-card {
    padding: var(--spacing-md);
  }

  .progress-info {
    flex-wrap: wrap;
    gap: var(--spacing-xs);
  }

  .progress-value {
    font-size: 1.2rem;
  }

  .onboarding-main-content {
    padding: var(--spacing-lg);
  }

  .section-content-header h3 {
    font-size: 1.4rem;
  }

  .section-navigation {
    flex-direction: column;
  }

  .nav-button {
    justify-content: center;
  }

  .modal-content {
    padding: var(--spacing-lg);
    max-height: 90vh;
  }
}
/* =========================
   HERO ( )
   ========================= */
.hero-section {
  min-height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-3xl) var(--spacing-lg);
  position: relative;
  overflow: hidden;
}

.hero-container {
  max-width: 960px;
  margin: 0 auto;
  text-align: center;
}

.hero-title {
  font-size: clamp(2.5rem, 5vw, 4rem);
  font-weight: 700;
  margin-bottom: var(--spacing-md);
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-subtitle {
  font-size: clamp(1.125rem, 2vw, 1.375rem);
  color: var(--text-secondary);
  max-width: 720px;
  margin: 0 auto var(--spacing-2xl);
  line-height: 1.6;
}

.hero-cta-group {
  display: flex;
  gap: var(--spacing-md);
  justify-content: center;
  flex-wrap: wrap;
}

.cta-button.primary {
  padding: 18px 36px;
  font-size: 1.125rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 24px rgba(102, 126, 234, 0.3);
}

.cta-button.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 32px rgba(102, 126, 234, 0.4);
}

.cta-button.secondary {
  padding: 18px 36px;
  font-size: 1.125rem;
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  color: white;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cta-button.secondary:hover {
  border-color: rgba(255, 255, 255, 0.4);
  background: rgba(255, 255, 255, 0.05);
}

/*  () */
.hero-decoration {
  position: absolute;
  inset: -20%;
  pointer-events: none;
  background: radial-gradient(
      40% 40% at 20% 20%,
      rgba(102,126,234,0.15) 0%,
      rgba(102,126,234,0) 60%
    ),
    radial-gradient(
      40% 40% at 80% 30%,
      rgba(118,75,162,0.15) 0%,
      rgba(118,75,162,0) 60%
    );
  z-index: 0;
}

/*   */
.fade-in {
  animation: fadeInHero 0.6s ease forwards;
  opacity: 0;
}
@keyframes fadeInHero {
  from { opacity: 0; transform: translateY(6px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Mobile */
@media (max-width: 768px) {
  .hero-cta-group {
    flex-direction: column;
    align-items: stretch;
  }
  .cta-button.primary,
  .cta-button.secondary {
