  gap: var(--spacing-sm);
}
.menu-item {
  background: transparent;
  border: 1px solid transparent;
  color: #dbe2ee;
  padding: 10px 12px;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: all .2s ease;
}
.menu-item:hover {
