.mobile-menu-toggle {
  display: none;
  background: transparent;
  border: none;
  color: #fff;
  padding: 8px;
  border-radius: 10px;
}
.mobile-menu-overlay {
  position: fixed;
  inset: 0;
  background: rgba(15, 15, 30, 0.6);
  z-index: 1000;
}
.mobile-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: min(88vw, 380px);
  height: 100vh;
  background: #0c0f1e;
  border-left: 1px solid rgba(255,255,255,0.1);
  z-index: 1001;
