/* =========================
   MATERIALS FAB + SIDEBAR
   ========================= */
.fab {
  position: fixed;
  right: 20px;
  bottom: 22px;
  z-index: 1002;
  width: 56px;
  height: 56px;
  border-radius: 16px;
  border: none;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
  display: grid;
  place-items: center;
  box-shadow: 0 10px 28px rgba(0,0,0,.35);
  cursor: pointer;
  transition: transform .15s ease, box-shadow .15s ease;
}
.fab:hover { transform: translateY(-1px); box-shadow: 0 12px 34px rgba(0,0,0,.42); }

.fab-overlay {
  position: fixed;
  inset: 0;
  background: rgba(15, 15, 30, 0.55);
  z-index: 1000;
}

.fab-sidebar {
  position: fixed;
  right: 20px;
  bottom: 88px;
  width: min(360px, 92vw);
  max-height: min(70vh, 560px);
  overflow: auto;
  background: #0c0f1e;
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 16px;
  box-shadow: 0 12px 36px rgba(0,0,0,.45);
  z-index: 1001;
  padding: 12px;
  transform: translateY(10px);
  opacity: 0;
  pointer-events: none;
  transition: opacity .15s ease, transform .15s ease;
}
.fab-sidebar.open {
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
}

.fab-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 6px 6px 8px;
  border-bottom: 1px solid rgba(255,255,255,0.08);
}
.fab-header h3 {
  margin: 0;
  font-size: 1rem;
  font-weight: 700;
}

.fab-list {
  display: grid;
  gap: 8px;
  padding-top: 10px;
}
.fab-item {
  display: grid;
  grid-template-columns: 36px 1fr 18px;
  gap: 10px;
  align-items: center;
  background: rgba(255,255,255,0.04);
  border: 1px solid transparent;
  border-radius: 12px;
  padding: 10px;
  color: #e8edf6;
  text-decoration: none;
  transition: border-color .15s ease, background .15s ease, transform .15s ease;
}
.fab-item:hover {
  background: rgba(255,255,255,0.06);
  border-color: rgba(255,255,255,0.14);
  transform: translateY(-1px);
}
.fab-item.disabled,
.fab-item:disabled {
  opacity: .7;
  cursor: not-allowed;
}
.fi-left {
  display: grid;
  place-items: center;
  width: 36px; height: 36px; border-radius: 10px;
  background: rgba(255,255,255,0.06);
}
.fi-title { font-weight: 700; }
.fi-subtitle { color: var(--text-secondary); font-size: .9rem; }
.fi-right { display: grid; place-items: center; }


