/* START HERE */
.start-here .section-header { text-align: center; }
.starthere-grid { display: grid; gap: 18px; grid-template-columns: repeat(3, 1fr); }
.start-card {
  background: #0c0f1e; border: 1px solid rgba(255,255,255,0.06);
  border-radius: 14px; padding: 18px; display: grid; gap: 14px;
  transition: transform .2s ease, box-shadow .2s ease, border-color .2s ease;
}
.start-card:hover { transform: translateY(-2px); box-shadow: 0 10px 30px rgba(0,0,0,.35); border-color: rgba(255,255,255,0.12); }
