@media (max-width: 720px) { .starthere-grid { grid-template-columns: 1fr; } }

/* CYCLE TIMELINE (scoped to avoid conflict with existing .timeline) */
.cycle-timeline .section-header { text-align:center; max-width:900px; margin:0 auto 24px; }
.cycle-timeline .timeline { position: relative; display: grid; gap: 16px; margin: 0; padding:0; list-style:none; }
.cycle-timeline .timeline-item { display:grid; grid-template-columns:24px 1fr; gap:14px; align-items:start; }
.cycle-timeline .timeline-marker { position:relative; height:100%; }
.cycle-timeline .timeline-marker::before { content:""; position:absolute; top:10px; bottom:-10px; left:11px; width:2px; background:linear-gradient(180deg, rgba(255,255,255,0.15), rgba(255,255,255,0.05)); }
.cycle-timeline .timeline-pin { width:24px; height:24px; border:2px solid rgba(255,255,255,0.3); background:#0c0f1e; border-radius:50%; box-shadow:0 0 0 4px rgba(118,75,162,0.2); }
.cycle-timeline .timeline-card { background:#0c0f1e; border:1px solid rgba(255,255,255,0.08); border-radius:14px; padding:16px; display:grid; gap:12px; transition:border-color .2s, box-shadow .2s, transform .2s; }
.cycle-timeline .timeline-card:hover { border-color:rgba(255,255,255,0.16); box-shadow:0 12px 32px rgba(0,0,0,.32); transform:translateY(-1px); }
.cycle-timeline .timeline-card-header { display:flex; align-items:center; gap:12px; }
.cycle-timeline .timeline-icon { width:44px; height:44px; border-radius:10px; background:rgba(255,255,255,0.06); display:grid; place-items:center; }
